// ==========================================================================
// Grid Container
// ==========================================================================

.grid {
    @extend %clearfix;
    @extend .centered;
    
    width: 90%;

    // Make nested grid 100%
    [class*="grid__col--"] > & {
        width: 100%;
    }

    // Set a max-width grid container
    @include mq(large) {
        max-width: $g-cont-max-w;
    }

}