// ==========================================================================
// Images
// ==========================================================================

.img {
    @include m(wrap) {
        border: 1px solid palette(grey, x-light);
        padding: em(12px);
    }

    @include m(avatar) {
        display: block;
        border-radius: $br--round;
    }

    @include m(hero) {
        margin-bottom: em(42px);
    }
}
